
import React, { Component } from 'react'
import './about.css';
import Nav from './nav/nav';
import { motion } from 'framer-motion';
import {Link} from 'react-router-dom';
import GLOBE from 'vanta/dist/vanta.globe.min.js'
import * as THREE from 'three'



 class Aboutme extends Component {
    constructor() {
        super()
        this.vantaRef = React.createRef()
      }
      componentDidMount() {
        this.vantaEffect = GLOBE({
          el: this.vantaRef.current,
          THREE: THREE,
          backgroundColor: 'rgb(0,0,0)',
          color: '#C7493A',
          scale: 1.00,
          scaleMobile: 1.00
        })
      }
      componentWillUnmount() {
        if (this.vantaEffect) {
          this.vantaEffect.destroy()
        }
      }


  render() {

    const pageTransition = {
        in: {
            translateY: '0%',
            opacity: 1
        },

        out: {
            translateY: '100%',
            opacity: 0
        },

    }

    return (
      <div ref= {this.vantaRef} style={{ position: 'relative' ,height: '100%'}}>
        <motion.div
          exit="out"
          animate="in"
          initial="out"
          variants={pageTransition}
          style={{ position: 'relative', transitionDuration: '0.4s', transitionTimingFunction: 'ease-out' }}
        >

          <div className="about-me-holder" >
            <div className="about-info" >
              <h1 className="introduction">Hi I am</h1>
              <h1>Virgilio Mumar Jr</h1>
              <p>Your Front-end Developer</p>
              <Link to = {'/contact'}>
              <button>Hire Now!</button>
              </Link>
             
            </div>
          </div>

        </motion.div>
        <Nav />
      </div>



    )
  }
}



export default Aboutme
