import React, { Component } from 'react'
import './profile.css';
import myPic from '../89435441_1079840042377151_4411706981006442496_o.jpg';
import html from '../html.png'
import css from '../css.png'
import js from '../javascript.png'
import node from '../node-js.png';
import react from '../react.png';
import sass from '../sass.png';
import moviepng from '../movie.png';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import socket1 from '../socket.jpg';
import socket2image from '../socket2.jpg';
import calc from '../calc.png';
import { Link } from 'react-router-dom'
import redux from '../redux.png'
import Nav from '../nav/nav';
import mongo from '../mongodb.png'
import { motion } from 'framer-motion';
import BIRDS from 'vanta/dist/vanta.birds.min.js';
import * as THREE from 'three';
class Profile extends Component {

    constructor() {
        super()
        this.vantaRef = React.createRef()
    }
    componentDidMount() {
        this.vantaEffect = BIRDS({
            el: this.vantaRef.current,
            THREE: THREE,
            backgroundColor: 'rgb(0,0,0)',
            minHeight: 100.00,
            minWidth: 100.00,
            scale: 1.5,
            scaleMobile: 1.5,
            color2: 0xC7493A,
            birdSize: 0.40,
            quantity: 4.00
        })
      
    }
    componentWillUnmount() {
        if (this.vantaEffect) {
            this.vantaEffect.destroy()
        }
    }


    render() {


        const pageTransition = {
            in: {
                translateY: '0%',
                opacity: 1
            },

            out: {
                translateY: '100%',
                opacity: 0
            },

        }
        return (

            <div ref={this.vantaRef} style={{ position: 'relative' }}>

                <motion.div exit="out"
                    animate="in"
                    initial="out"
                    variants={pageTransition}

                    style={{ position: 'relative', transitionDuration: '.4s', transitionTimingFunction: 'ease-out' }}>

                    <div className="profile-landing">
                        <div className="profile-header"><h1><span className="profile-number">-01</span> ABOUT ME</h1></div>
                        <div className="profile-photo">

                            <div className="rotating">
                                <img src={myPic} alt="me" />
                            </div>
                            <p class="profile-info">Hi I am <span>VIRGILIO MUMAR JR</span>and I am a <span>front end Developer</span> but i know a little bit of <span>Mern Stack</span> and I am always eager to learn other programming languages and i am a very hardworking person... I have experience working on website builders like <span>KAJABI/KARTRA/AND WORDPRESS.</span> I like building websites because I think coding was an art like drawing something and putting color on it to <span>make it fantastic.</span></p>
                            <Link to={'/contact'}>
                                <button class="profile-info-button">Hire Now!</button>
                            </Link>
                        </div>
                    </div>
                    <div className="profile-Skills">
                        <div className="profile-header"><h1><span className="profile-number">-02</span> SKILL SET</h1></div>
                        <div className="profile-skill-img">
                            <img src={html} alt="html" />
                            <img src={css} alt="css" />
                            <img src={js} alt="js" />
                            <img src={react} alt="react" />
                            <img src={node} alt="node" />
                            <img src={sass} alt="sass" />
                            <img src={redux} alt="redux" />
                            <img src={mongo} alt="mongo" />

                        </div>
                    </div>

                    <div className="profile-projects">
                        <div className="profile-header"><h1><span className="profile-number">-03</span> PROJECTS</h1></div>
                        <div className="project">
                            <div className="project-images">
                                <Slider>

                                    <div className="slide-image">
                                        <img src={socket2image} />
                                    </div>
                                    <div className="slide-image">
                                        <img src={socket1} />
                                    </div>
                                </Slider>
                            </div>

                            <div className="profile-projects-info">
                                <h1>FREE FOR ALL CHAT WEBSITE</h1>
                                <p>I created this with SOCKET.IO, NODE.JS, REACT JS, AND EXPRESS... in this app you can receive and send messages in realtime <br /><br /><span style={{ fontSize: '20px', color: '#C7493A' }}>...SLIDE THE IMAGE</span></p>
                            </div>
                        </div>

                        <div className="project">
                            <div className="project-images-2">
                                <img src={moviepng} />
                            </div>

                            <div className="profile-projects-info">
                                <h1>MOVIE SEARCHING WEBSITE</h1>
                                <p>I created this with REACT JS and Movie List API ... in this app you can search all the movie. <br /><br /><span style={{ fontSize: '20px', color: '#C7493A' }}>...SCROLL THE IMAGE</span></p>
                            </div>
                        </div>


                        <div className="project">
                            <div style={{ display: 'flex', justifyContent: 'center' }} className="project-images">
                                <img src={calc} />
                            </div>

                            <div className="profile-projects-info">
                                <h1>CALCULATOR</h1>
                                <p>I created this with REACT JS ... in this app you can calculate basic math like ADDITION,SUBTRACTION,<br />MULTIPLACTION,DIVISION</p>
                            </div>
                        </div>
                    </div>

                </motion.div>
                <Nav />
            </div>

        )
    }
}

export default Profile
