import React, { Component } from 'react';
import './contact.css';
import FacebookIcon from '@material-ui/icons/Facebook';
import EmailIcon from '@material-ui/icons/Email';
import PermPhoneMsgIcon from '@material-ui/icons/PermPhoneMsg';
import Nav from './nav/nav';
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';
import { motion } from 'framer-motion';
import GLOBE from 'vanta/dist/vanta.globe.min.js'
import * as THREE from 'three'

class Contact extends Component {

    constructor() {
        super()
        this.vantaRef = React.createRef()
      }
      componentDidMount() {
        this.vantaEffect = GLOBE({
          el: this.vantaRef.current,
          THREE: THREE,
          backgroundColor: 'rgb(0,0,0)',
          color: '#C7493A',
          scale: 1.00,
          scaleMobile: 1.00
        })
      }
      componentWillUnmount() {
        if (this.vantaEffect) {
          this.vantaEffect.destroy()
        }
      }

    
    render() {
      

        const pageTransition = {
            in: {
              translateY: '0%',
              opacity: 1
            },
      
            out: {
              translateY: '100%',
              opacity: 0
            },
      
          }

        return (
            <div ref= {this.vantaRef} className = "contact-holder" style = {{position: 'relative'}}>
    
               <motion.div
                exit="out"
                animate="in"
                initial="out"
                variants={pageTransition}
                style={{ position: 'relative', transitionDuration: '0.4s', transitionTimingFunction: 'ease-out' }}

               className = "contact">
                   <h1 className = "contact-h1">CONTACT</h1>
                  <ul>
                      <li><h1><FacebookIcon className = "svg"  /> FACEBOOK</h1><a style = {{cursor: 'pointer'}} href = "https://www.facebook.com/profile.php?id=100010534967827" target = "_blank" rel="noopener noreferrer"><KeyboardArrowRightIcon />https://www.facebook.com/profile.php?id=100010534967827</a></li>
                      <li><h1><EmailIcon className = "svg" /> GMAIL</h1><a style = {{cursor: 'text'}}><KeyboardArrowRightIcon /> pinoyvj39@gmail.com</a></li>
                      <li><h1><PermPhoneMsgIcon className = "svg" /> Phone Number</h1><a style = {{cursor: 'text'}} ><KeyboardArrowRightIcon /> 09456897815</a></li>
                  </ul>
               </motion.div>
               <Nav />
            </div>
        )
    }
}

export default Contact
